<template>
  <PCForum2 :id="this.id" />
</template>

<script>
import PCForum2 from "@/components/pc_forum2.vue";
export default {
  name: "Forum2",
  props: ["id"],
  components: {
    PCForum2,
  },
  mounted() {
    this.onChange();
  },
  watch: {
    $route: "onChange",
  },
  beforeRouteUpdate(to, from, next) {
    // 
    //  /foo/:id /foo/1  /foo/2 
    //  Foo 
    //  `this`
    if (from.name == "Post") {
    } else {
      this.$store.commit("setRowIndex", 0);
    }
    next();
  },
  methods: {
    onChange() {
      // this.$store.commit("setRowIndex", 0);
    },
  },
};
</script>


